@import 'libs/reset.scss';

body {
  background: #fff;
  font-size: 18px;
  font-family: Inter, sans-serif;
  font-weight: 400;
}

.body--active {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #222934;
  display: inline-block;
}

img {
  display: inline-block;
  object-fit: cover;
  border-radius: 5px;
}

a:hover {
  color: #124594;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.spacer {
  margin-top: 160px;
}

.hero {
  position: relative;
  &__navigation {
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 62px;
    display: flex;
    align-items: center;
    &-link {
      &:first-child {
        margin-left: 0;
      }
      margin-left: 40px;
    }
  }
  &__menu {
    display: none;
    height: 20px;
    width: 34px;
    position: relative;
    span {
      transition: 0.3s all ease-out;
      position: absolute;
      width: 34px;
      height: 1px;
      display: block;
      background: #000;
      &:nth-child(2) {
        top: 10px;
      }
      &:nth-child(3) {
        top: 20px;
      }
    }
  }
  &__search {
    cursor: pointer;
  }
  &__logo {
    position: absolute;
    top: 40px;
    left: 40px;
  }
  &__search {
    position: absolute;
    top: 62px;
    right: 40px;
  }
}

.blog-list {
  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__list-item {
    width: 270px;
    margin-top: 60px;
    p {
      margin-top: 15px;
      font-size: 14px;
      color: #4d4d4d;
    }
  }

  &__list-image {
    img {
      pointer-events: none;
      height: 280px;
      object-fit: cover;
      width: 100%;
      border-radius: 5px;
    }
  }

  &__list-title {
    margin-top: 15px;
    font-weight: 600;
    color: #222934;
  }

  &__list-item--long {
    width: 580px;
    img {
      height: 240px;
    }
  }
}

.h1 {
  &::after {
    margin-top: 15px;
    content: '';
    width: 100%;
    height: 2px;
    display: block;
    background: #124594;
  }
}
.h1__title {
  display: flex;
  justify-content: space-between;
  margin: 0;
}

h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 42px;
}

.search-modal {
  &--active {
    opacity: 1;
    z-index: 9999;
  }
  position: fixed;
  transition: 0.2s all ease-out;
  opacity: 0;
  z-index: -1;
  top: 0;
  left: 0;
  background: #15181d;
  width: 100%;
  height: 100%;
  &__field {
    opacity: 0;
    &--fadeInDown {
      opacity: 1;
      animation: fadeInDown 1 0.5s ease-out;
    }
    animation: fadeOutDown 1 0.5s ease-out;
    margin: 0 auto;
    margin-top: 266px;
    width: 712px;
    position: relative;
    input {
      padding: 0;
      width: 712px;
      color: #fff;
      border: none;
      background: transparent;
      font-style: normal;
      font-weight: normal;
      font-size: 25px;
      line-height: 120%;
      padding-bottom: 15px;
      border-bottom: 2px solid #888888;
      transform: 0.3s all ease-out;
      &:focus {
        border-color: #fff;
      }
    }
  }
  &__btn {
    position: absolute;
    right: 0;
    padding: 0;
    margin: 0;
    bottom: 15px;
    width: 36px;
    height: 36px;
    background-color: transparent !important;
    border: none;
  }
  &__close {
    position: absolute;
    right: 40px;
    top: 62px;
    cursor: pointer;
    transition: 0.3s all ease-out;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeOutDown {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
}

@media (max-width: 1750px) {
  .hero {
    padding-top: 20px;
    position: initial;
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    &__navigation {
      padding-top: 0;
      max-width: unset;
      margin-left: 40px;
    }
    &__logo {
      position: unset;
    }
    &__search {
      position: unset;
    }
  }
}

@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
  .hero {
    max-width: 960px;
  }
  .search-modal {
    &__close {
      top: 43px;
    }
    &__field {
      margin-top: 176px;
    }
  }
  .blog-list {
    &__list-item {
      width: calc(100% / 3 - 40px) !important;
    }

    &__list-image {
      img {
        height: 240px;
      }
    }

    &__list-item--long {
      width: inherit;
    }
  }
}

@media (max-width: 960px) {
  .container {
    max-width: 100%;
    padding: 0 20px;
  }
  .spacer {
    margin-top: 60px;
  }
  .search-modal {
    &__close {
      right: 20px;
    }
    &__field {
      width: 100%;
      input {
        width: 100%;
      }
    }
  }
  .hero {
    max-width: 100%;
    padding: 20px 20px 0 20px;
    position: relative;
    justify-content: space-between;
    &__menu {
      display: block;
      &--active {
        z-index: 9999;
        span {
          background: #fff;
          &:nth-child(2) {
            opacity: 0;
          }
          &:nth-child(1) {
            top: 10px;
            transform: rotate(-45deg);
          }
          &:nth-child(3) {
            top: 10px;
            transform: rotate(45deg);
          }
        }
      }
    }
    &__navigation {
      transition: 0.3s all ease-out;
      &--active {
        opacity: 1;
        z-index: 999;
      }
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      margin: 0;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background: #15181d;
      opacity: 0;
      z-index: -1;
      &-link {
        margin: 20px;
        font-size: 25px;
        color: #fff;
        &:first-child {
          margin-left: 20px;
        }
      }
    }
  }
  .blog-list {
    &__list-item {
      width: calc(100% / 2 - 40px) !important;
    }
  }
}

@media (max-width: 624px) {
  h1 {
    font-size: 25px;
  }
  .search-modal {
    &__field {
      margin-top: 130px;
    }
  }
  .blog-list {
    &__list-item {
      margin-top: 40px;
      width: calc(100% / 2 - 10px) !important;
      p {
        margin-top: 5px;
        font-size: 13px;
      }
    }
    &__list-image {
      img {
        height: 130px;
      }
    }
    &__list-title {
      margin-top: 5px;
      font-size: 15px;
    }
  }
}

@media (max-width: 320px) {
  .search-modal {
    &__field {
      margin-top: 120px;
    }
  }
}

.more {
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  svg {
    margin-left: 15px;
  }
}

.blog {
  .container {
    max-width: 800px;
  }
  img {
    width: 100%;
    object-fit: cover;
    border-radius: 5px;
  }
  &__title {
    margin-bottom: 40px;
  }

  &__image {
    margin-bottom: 40px;
  }

  &__inf {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 36px;
  }

  &__inf-date {
    color: #4d4d4d;
  }

  &__inf-social {
    &:last-child {
      margin-left: 15px;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
  }

  &__tag {
    margin-bottom: 30px;
    padding: 12px 20px;
    background: #dcdcdc;
    margin-right: 20px;
    border-radius: 5px;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #4d4d4d;
    transition: 0.3s all ease-out;
    &:hover {
      background: #4d4d4d;
      color: #fff;
    }
    &:first-child {
      margin-left: 0;
    }
  }

  &__content {
    margin-top: 30px;
    p {
      &:first-child {
        margin-top: 0;
      }
      line-height: 140%;
      margin-top: 20px;
    }
    hr {
      margin-top: 60px;
      border: none;
    }
  }
  &__author {
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__author-title {
    font-weight: 600;
    font-size: 18px;
    line-height: 120%;
    color: #222934;
  }

  &__author-name {
    font-size: 14px;
    line-height: 140%;
    color: #4d4d4d;
  }
}

.read-more {
  &__items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__item {
    margin-top: 60px;
    width: calc(100% / 3 - 40px);
  }

  &__item-image {
    img {
      border-radius: 5px;
      width: 100%;
      height: 280px;
      display: inline;
    }
  }

  &__item-title {
    margin-top: 15px;
    font-weight: 600;
    font-size: 18px;
    line-height: 120%;
    color: #222934;
  }
  p {
    margin-top: 15px;
    font-size: 14px;
    line-height: 17px;
    color: #4d4d4d;
  }
}

@media (max-width: 1200px) {
  .blog {
    .container {
      max-width: 960px;
    }
  }
}

@media (max-width: 960px) {
  .blog {
    .container {
      max-width: 100%;
    }
    &__tag {
      margin-bottom: 20px;
    }
    &__content {
      margin-top: 20px;
      hr {
        margin-top: 40px;
      }
    }
  }
  .read-more {
    &__item {
      width: calc(100% / 2 - 40px);
    }
  }
}

@media(max-width:624px) {
  .blog {
    &__author {
      margin-top: 60px;
      flex-direction: column;
      align-items: start;
    }
    &__author-name {
      margin-top: 5px;
    }
  }
  .read-more {
    &__item {
      margin-top: 40px;
      width: calc(100% / 2 - 20px);
    }
    &__item-title {
      margin-top: 5px;
      font-size: 15px;
    }
    p {
      margin-top: 5px;
      font-size: 13px;
    }
    &__item-image {
      img {
        height: 130px;
      }
    }
  }
}